@import '../../common/styles/Variablies';

.headerComponent {
  background-color: $bgColor;
  margin-bottom: 25px;

  .headerBox {
    margin: 0 auto;
    width: 1100px;
    height: 56px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;

    @media (max-width: 1140px) {
      width: 100%;
      padding-left: 20px;
      padding-right: 20px;
    }

    @media (max-width: 700px) {
      height: 107px;
    }

    @media (max-width: 500px) {
      padding-left: 10px;
      padding-right: 10px;
    }

    .searchBox {
      width: 300px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      position: relative;

      @media (max-width: 700px) {
        width: 100%;
        margin-top: 6px;
      }

      .image {
        height: 45px;
      }

      .searchInput {
        height: 38px;
        width: 80%;
        font-size: 12px;
        border: none;
        border-radius: 5px;
        outline: none;
        padding-left: 10px;
        padding-right: 50px;
        color: $colorSearch;

        @media (max-width: 700px) {
          margin-left: 15px;
          width: 100%;
        }
      }

      .searchButton {
        position: absolute;
        right: 0;
        width: 38px;
        height: 38px;
        outline: none;
        border: none;
        border-radius: 0 5px 5px 0;
        background-color: $bgButton;
        cursor: pointer;

        svg {
          fill: $bgColor;
        }
      }
    }
  }
}
